.about {
  @include onDesktop {
    margin-bottom: 160px;
  }

  &__title {
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;
    margin-bottom: 32px;

    @include onTablet {
      font-size: 48px;
    }

    @include onDesktop {
      font-size: 64px;
    }
  }

  &__paragraph {
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;

  }
}
